.app__laurels_awards {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  margin-top: 3rem;
}

.app__laurels_awards-card {
  flex: 1;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  min-width: 230px;
  margin: 1rem;
}

.app__laurels_awards-card img {
  margin-right: 1rem;
  width: 50px;
  height: 50px;
}

@media screen (min-width: 1900px) {
  .app__laurels_awards-card {
    min-width: 390px;
  }
}

@media screen (max-width: 450px) {
  .app__laurels_awards-card {
    min-width: 100%;
    margin: 1rem 0;
  }
}
